<template>
    <v-parallax class="text-center" :height="height" :src="img">
        <h1 v-if="title != ''" class="header-title">{{ title }}</h1>
        <slot />
    </v-parallax>
</template>

<script>
export default {
    name: "Header",
    props: {
        title: {
            type: String,
            required: false,
            default: ''
        },
        img: {
            type: String,
            required: false,
            default: ''
        },
        height: {
            type: Number,
            required: false,
            default: 340
        }
    }
}
</script>

<style>
.header-title {
    font-size: 3em;
    text-shadow: 3px 3px 4px #222;
}
</style>